/* WearerSIMCard.module.css */
.generalContainer {
    @apply flex flex-col bg-gradient-to-b from-purple-custom1 to-purple-custom2 rounded-3xl shadow-sm p-4 mb-4 w-full;
}

.firstRow {
    @apply flex justify-between content-start
}

.leftContainer {
    @apply ms-4 flex;
}
  
.textContainer {
    @apply flex flex-col content-start;
}
  
.title {
    @apply text-xl font-bold text-start text-white mb-2;
}

.btn {
    @apply bg-[#32B8C0] rounded-xl text-white text-base p-0.5 mt-8
}

.hardwareDesc {
    @apply text-sm text-start text-white;
}

.imgContainer {
    @apply flex mr-4
}
  
.image {
    @apply w-64 h-52;
}

.verticalSpace {
    @apply flex h-2
}

.rightCol {
    @apply flex flex-col justify-end content-end
}

.refreshSuperContainer {
    @apply flex justify-end
}

.refreshContainer {
    @apply border-white border-2 rounded-xl py-1.5 px-3.5 cursor-pointer w-12 flex justify-center
}

.refreshImg {
    @apply w-4 h-4
}

.secondRow {
    @apply bg-[#8A66DA] rounded-3xl w-full flex flex-col
}

.btnsSubset {
    @apply w-full flex flex-row
}

.missingInfo {
    @apply text-[#d8c8ff]
}

.terminated {
    @apply text-[#ff8eb4]
}
