.comandTitle2 {
    @apply text-base text-[#603BB0] text-start mb-2 mt-4
}

.inputContainer {
    @apply w-full
}

.textBox{
    @apply w-full border-2 border-slate-300 rounded-lg p-1.5
}

.selectBox {
    @apply w-full border-2 border-slate-300 rounded-md
}

.space {
    @apply flex w-3
}

.sendIcon {
    @apply flex items-center px-2 rounded-3xl aspect-square hover:bg-slate-300 hover:cursor-pointer
}

.shutDownBtn {
    @apply bg-[#F93C7C] text-white p-1 rounded-full w-full mt-2 mb-2
}

.shutDownBtn:disabled {
    @apply bg-[#f199b6]
}

.pausedBtn {
    @apply bg-[#32B8C0] text-white p-1 rounded-full w-full mt-2 mb-2
}

.cancelBtn {
    @apply border-[#F93C7C] text-[#F93C7C]
}

.okBtn {
    @apply bg-[#8A66DA] border-[#8A66DA] text-white !important
}

.okBtn:disabled {
    @apply bg-[#b7a7da] border-[#b7a7da] !important
}

.textArea {
    @apply mt-3
}